import { props, prop, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import styles from "./icon.less"
import NoShadowRoot from "../../utils/NoShadowRoot"

const Component = withComponent(withPreact());
const React = { createElement: h }

class DnxIcon extends NoShadowRoot(Component) {
  static get is() {
    return "dnx-icon"
  }
  static props = {
    name: props.string,
    color: props.string,
    size: props.string
  }
  /*
  static get props() {
    return {
      name: prop.string({
        attribute: true,
        default: "dna-radio-disabled"
      }),
      color: prop.string({
        attribute: true,
        default: "#333"
      }),
      size: prop.string({
        attribute: true,
        default: "16px"
      })
    }
  }
*/
  renderCallback( { name = "dna-radio-disabled", color = "#333", size = "16px" }) {
    //const { name, color, size } = this
    const _styles = styles
    return (
      <div class="dnx-icon-holder">
        <style>{_styles}</style>
        <span class={name} style={`color:${color};font-size:${size};`} />
      </div>
    )
  }
}

define(DnxIcon.is, DnxIcon)
export default DnxIcon



