import define from "../../utils/define"
/**
 * Constructor for DnxPopOver
 * @memberof DnxPopOver
 * @attribute {string} id - popover / this._tooltip id. target container & this._tooltip container's ids have to be matched.
 * @attribute {string} placement - Location of the popover / this._tooltip
 * @attribute {string} offset - shift the pointer (i.e: left padding puts target to be right side. offset="20px")
 * @attribute {string} width - 500 (for 500px)
 */

const fragment = document.createElement('template')
fragment.innerHTML = `
        <div class="dnx-popover-component">
            <div class="dnx-popover-message"></div>
            <div class="pointer"></div>
        </div>
`

class PopOver extends HTMLElement {

    static get is() {
        return "dnx-popover"
    }

    constructor() {
        super()
        this._show = this._show.bind(this)
        this._hide = this._hide.bind(this)
        this._tooltip = null
        //CSS values
        this._PADDING = 24
        this._BORDER = 1
        this._ARROW_WIDTH_V, this._ARROW_HEIGHT_V = this._ARROW_WIDTH_V = 14
        this._ARROW_WIDTH_H, this._ARROW_HEIGHT_H = this._ARROW_WIDTH_H = 28
    }

    connectedCallback() {
        if (!this.hasAttribute('role'))
            this.setAttribute('role', 'popover')

        if (!this.hasAttribute('tabindex'))
            this.setAttribute('tabindex', -1)

        this._hide()

        this._targetContainer = document.querySelector('[aria-describedby=' + this.id + ']')
        this._target = document.querySelector('[aria-describedby=' + this.id + ']');

        if (!this._targetContainer)
            return

        this.setAttribute('class', 'dnx-popover')
        //Location of the Popover/Tooltip - set default to the 'top'
        this._placement = this.hasAttribute('placement') ? this.getAttribute('placement') : 'top'


        const popoverNode = fragment.content.cloneNode(true)

        const contentDiv = popoverNode.querySelector('.dnx-popover-message')
        contentDiv.appendChild(this.childNodes[0])
        this.appendChild(popoverNode)

        //set reference to appended node
        this._tooltip = this.querySelector('.dnx-popover-component')

        const tartgetElNodes = [...this._targetContainer.childNodes]
        //get first child of non text/comment html node - target node
        this._target = tartgetElNodes.find(n => (n.nodeType !== 8 && n.nodeType !== 3))

        this._targetRect = this._target.getBoundingClientRect()
        let _width = this.hasAttribute('width') ? Number(this.getAttribute('width')) : null

        if (_width) {
            //rip out px if it's string
            _width = isNaN(Number(_width)) ? Number(this.hasAttribute('width').slice(0, -2)) : _width

            if (this._placement === 'left') {
                if (_width < (document.body.clientWidth - (document.body.clientWidth - this._targetRect.x)))
                    this._tooltip.style.width = `${_width}px`
                else
                    this._tooltip.style.width = `${document.body.clientWidth - this._targetRect.x}px`
            } else {
                if (_width < (document.body.clientWidth - this._targetRect.x))
                    this._tooltip.style.width = `${_width}px`
                else
                    this._tooltip.style.width = `${document.body.clientWidth - this._targetRect.x}px`
            }
        }

        this._targetContainer.style.position = "relative" //this helps to get the right target

        /**
         * shift the pointer if aksed 
         */
        const _pointer = this.querySelector('.pointer')
        const offsetVal = this.hasAttribute('offset') ? this.getAttribute('offset') : null

        switch (this._placement) {
            case 'right':
                this._tooltip.classList.add('right')
                this._tooltip.style.left = `${this._targetRect.width + this._ARROW_WIDTH_V}px`
                offsetVal ? _pointer.style.top = offsetVal : null
                break
            case 'left':
                this._tooltip.classList.add('left')
                this._tooltip.style.right = `${this._targetRect.width + this._ARROW_WIDTH_V}px`;

                offsetVal ? _pointer.style.top = offsetVal : null
                break
            case 'bottom':
                this._tooltip.classList.add('bottom')
                this._tooltip.style.left = `${this._targetRect.width / 2 - this._PADDING - (this._ARROW_WIDTH_H / 2)}px`
                offsetVal ? _pointer.style.left = offsetVal : null
                break
            default: //'TOP'
                this._tooltip.classList.add('top')
                this._tooltip.style.left = `${this._targetRect.width / 2 - this._PADDING - (this._ARROW_WIDTH_H / 2)}px`
                offsetVal ? _pointer.style.left = offsetVal : null
                break
        }
        this._target.addEventListener('focus', this._show)
        this._target.addEventListener('blur', this._hide)
        this._target.addEventListener('mouseenter', this._show)
        this._target.addEventListener('mouseleave', this._hide)
    }

    /**
     * unregisters the event listeners that were set up in  connectedCallback().
     */
    disconnectedCallback() {
        if (!this._target)
            return

        this._target.removeEventListener('focus', this._show)
        this._target.removeEventListener('blur', this._hide)
        this._target.removeEventListener('mouseenter', this._show)
        this._target.removeEventListener('mouseleave', this._hide)
        this._target = null
        this._targetContainer = null

        this._tooltip && this.removeChild(this._tooltip)
        this._tooltip = null;
    }

    _show() {
        // this.hidden = false
        this.style.opacity = 1
        const _tooltipRect = this._tooltip.getBoundingClientRect()

        //calculate tooltip dimension when it shows up
        switch (this._placement) {
            case 'right':
                this._tooltip.style.top = `-${this._PADDING - (this._targetRect.height / 2) + (this._ARROW_HEIGHT_V)}px`
                break
            case 'left':
                this._tooltip.style.top = `-${this._PADDING - (this._targetRect.height / 2+3) + (this._ARROW_HEIGHT_V)}px`
                break
            case 'bottom':
                this._tooltip.style.top = `${this._ARROW_HEIGHT_H + 2}px`
                break
            default: //'top'
                this._tooltip.style.top = `-${_tooltipRect.height + this._ARROW_HEIGHT_V}px`;
                break
        }
    }

    _hide() {
        // this.hidden = true;
        this.style.opacity = 0
    }
}

define(PopOver.is, PopOver)
export default PopOver



