export default (Base = HTMLElement) => {
      return class extends Base {
        // This alone should work for most cases.
        get shadowRoot () {
        return this;
        }

        // However you might need this for those
        // that use the return value instead
        // of the property.
        attachShadow () {
        return this;
        }
    };
}



