import { props, prop, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define";
import PopOverBlockStyles from './PopOverBlockStyles.less';
import NoShadowRoot from "../../utils/NoShadowRoot"

const Component = withComponent(withPreact());
const React = { createElement: h }

class PopOverBlock extends NoShadowRoot(Component) {
    constructor(){
        super();
        this.onHover = this.onHover.bind(this);
    }

    static get is() {
        return "dnx-popover-block"
    }
    /**
     * This component slices a full height sidebar from the right and displays the content of the children. It also allows interaction with the main content.
     * @memberof Sidebar
     * @param {string} title Label displayed on top of the Sideba
     * @param {string} width Size of the sidebar based on the percentage of the windows size. It defaults to 55%.
     * @param {function} onClose Function for the close action of the sidebar
     */

    static get props() {
        return {
            popovertext: prop.string({ attribute: true, default: ''}),
            width: prop.number({ attribute: true, default: 336}),
            ispopoveractive: prop.boolean({ attribute: true, default: false}),
        }
    }

    onHover() {
        this.ispopoveractive = !this.ispopoveractive;
    }

    renderCallback() {
        const { popovertext, width, ispopoveractive } = this;
        return [
            Style(this, PopOverBlockStyles),
            <div class="dnx-popover-block">
                <dnx-popover
                    text={popovertext}
                    active={ispopoveractive}
                    width={width}
                />
                <div
                    className="popover-info-icon-block"
                    onmouseover={this.onHover}
                    onmouseout={this.onHover}>
                    <dnx-icon
                        id="popover-info-icon"
                        name={"dna-info-tip-filled"}
                    />
                </div>
            </div>
        ];
    }
};

define(PopOverBlock.is, PopOverBlock);

export default PopOverBlock;



