import { Component, h, prop, emit } from "skatejs"
import define from "../../utils/define"

import * as defaults from './defaults'

// Mixin to extend no shadow root.
function NoShadowRoot(Base = HTMLElement) {
    return class extends Base {
        // This alone should work for most cases.
        get shadowRoot() {
            return this;
        }

        // However you might need this for those
        // that use the return value instead
        // of the property.
        attachShadow() {
            return this;
        }
    };
}

// For JSX
const React = { createElement: h }

class DnxLineChart extends NoShadowRoot(Component) {
    static get is() {
        return "dnx-line-chart"
    }

    static get props() {
        return {
            width: prop.string({
                attribute: true,
                default: defaults.dimensions.width
            }),
            height: prop.string({
                attribute: true,
                default: defaults.dimensions.height
            }),
            data: prop.object({
                attribute: true,
                default: { categories: [], series: [] }
            }),
            scrollbar: prop.boolean({
                attribute: true,
                default: false
            }),
            cursor: prop.boolean({
                attribute: true,
                default: false
            }),
            colors: prop.array({
                attribute: true,
                default: Object.values(defaults.colors)
            }),
            legends: prop.boolean({
                attribute: true,
                default: true
            }),
            yAxisTitle: prop.string({
                attribute: true,
                default: ''
            }),
            noDataMessage: prop.string({
                attribute: true,
                default: defaults.noDataMessage
            }),
            smooth: prop.boolean({
                attribute: true,
                default: true
            }),
            animation: prop.boolean({
                attribute: true,
                default: false
            }),
            exportEnabled: prop.boolean({
                attribute: true,
                default: false
            }),
        }
    }

    updatedCallback(previousProps) {
        // The 'previousProps' will be undefined if it is the initial render.
        if (!previousProps) {
            return true;
        }

        // // The keys are the prop names, these can be String or Symbol.
        // const namesAndSymbols = Object.getOwnPropertySymbols
        //   ? Object.getOwnPropertySymbols(previousProps).concat(Object.getOwnPropertyNames(previousProps))
        //   : Object.getOwnPropertyNames(previousProps);

        // // The 'previousProps' will always contain all of the keys.
        // for (let nameOrSymbol of namesAndSymbols) {
        //   // With Object.is NaN is equal to NaN
        //   if (!Object.is(previousProps[nameOrSymbol], elem[nameOrSymbol])) {
        //     return true;
        //   }
        // }

        // Selective re-rendering using amCharts API only when data attrib changes 
        if (!Object.is(previousProps["data"], this["data"])) {
            //return true;
            if (this.chart) {
                this.chart.graphs = this.getGraphs()
                const newData = this.getData()
                if (this.animation) {
                    this.chart.animateData(newData, { duration: 300 })
                } else {
                    this.chart.dataProvider = newData
                    this.chart.validateData()
                }
                // TODO : handle empty data
            }
        }
        // No rerender otherwise
        return false
    }

    renderCallback() {
        const { width, height } = this
        return (
            <div>
                <div class="line-chart" style={`width: ${width}; height: ${height};`}></div>
            </div>
        )
    }

    renderedCallback() {
        if (!this.chart) {
            let config = this.getConfig()
            this.chart = AmCharts.makeChart(this.shadowRoot.querySelector('.line-chart'), config)
            // Example event
            this.emitEvent("dataUpdated")
        }
    }

    emitEvent(evtName) {
        this.chart.addListener(evtName, (e) => {
            emit(this, evtName)
        })
    }

    getGraphs() {
        const { smooth, data } = this
        let graphType = smooth ? "smoothedLine" : "line"
        let graphs = []
        for (let i = 0; i < data.series.length; i++) {
            let columnVal = i + 1;
            let graph = {
                "balloonText": "[[title]] of [[category]]:[[value]]",
                "type": graphType,
                "id": "AmGraph-" + columnVal,
                "title": data.series[i].name,
                "valueField": data.series[i].name,
                "lineThickness": 2
            }
            graphs.push(graph);
        }
        return graphs;
    }

    getData() {
        const _data = this.data
        let data = []
        for (let i in _data.categories) {
            let obj = {}
            for (let j in _data.series) {
                obj[_data.series[j].name] = _data.series[j].data[i]
            }
            obj['category'] = _data.categories[i]
            data.push(obj)
        }
        return data
    }

    getConfig() {
        const { yAxisTitle, cursor, scrollBar, legends, colors, exportEnabled } = this
        const marginTop = exportEnabled ? 60 : 20
        const graphs = this.getGraphs()
        const data = this.getData()
        return {
            "type": "serial",
            "categoryField": "category",
            "theme": "light",
            "marginTop": marginTop,
            "trendLines": [],
            "graphs": graphs,
            "guides": [],
            "color": defaults.fontColors["slate-grey-two"],
            "fontSize": 12,
            "valueAxes": [{
                "title": yAxisTitle,
                "titleBold": false,
                "axisAlpha": 0,
                "position": "left"
            }],
            "categoryAxis": {
                "gridPosition": "start",
                "gridAlpha": 0,
                "axisAlpha": 0,
                // "startOnAxis": true
            },
            "chartCursor": {
                "enabled": cursor,
                "categoryBalloonDateFormat": "JJ:NN"
            },
            "chartScrollbar": {
                "enabled": scrollBar
            },
            "responsive": {
                "enabled": true
            },
            "allLabels": [],
            "balloon": {},
            "legend": {
                "enabled": legends,
                "markerType": "round",
                "markerSize": 6,
                "spacing": -40,
                "align": "center",
                "color": defaults.fontColors["slate-grey-two"]
            },
            /*
            "export": {
                "enabled": exportEnabled,
                "menu": [{
                    "class": "export-main",
                    "menu": [{
                        "label": "Download",
                        "menu": ["PNG", "JPG", "CSV", "XLSX", "PDF"]
                    }, {
                        "format": "PRINT",
                        "label": "Print Chart"
                    }]
                }]
            },*/
            "colors": colors,
            "dataProvider": data,
            // Hack to load images, TODO: fix it
            "pathToImages": "http://cdn.amcharts.com/lib/3/images/",
        }
    }

}

define(DnxLineChart.is, DnxLineChart)
export default DnxLineChart



