import { props, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import NoShadowRoot from "../../utils/NoShadowRoot"
import Calendar from "./libs/Calendar"
import moment from "../../libs/vendor/moment/moment"

const Component = withComponent(withPreact());
const React = { createElement: h }
const INVALID = "Invalid date"

class DatePicker extends NoShadowRoot(Component) {

  constructor() {
    super();
    this.handleClick = this.handleClick.bind(this)
  }

  static get is() {
    return "dnx-date-picker"
  }

  static props = {
    styles: { ...props.string, ...{ attribute: true, default: '' } },
    active: { ...props.boolean, ...{ attribute: true, default: false } },
    value: { ...props.string, ...{ attribute: true, default: '' } },
    mm: { ...props.string, ...{ attribute: true, default: '' } },
    dd: { ...props.string, ...{ attribute: true, default: '' } },
    yyyy: { ...props.string, ...{ attribute: true, default: '' } },
    error: { ...props.string, ...{ attribute: true, default: '' } },
    errortext: { ...props.string, ...{ attribute: true, default: INVALID } },
    min: { ...props.string, ...{ attribute: true, default: '' } },
    max: { ...props.string, ...{ attribute: true, default: '' } },
    validate: { ...props.boolean, ...{ attribute: true, default: false } }
  }

  setValue(v) {
    const _date = new Date(v)
    const d = moment(_date)
    if (d.isValid()) {
      this.mm = moment(d).format('MM')
      this.dd = moment(d).format('DD')
      this.yyyy = moment(d).format('YYYY')
      if (this.calendar) this.calendar.setDate(_date, true)
    } else {
      const r = this.shadowRoot
      this.mm = r.querySelector('.MM').value
      this.dd = r.querySelector('.DD').value
      this.yyyy = r.querySelector('.YYYY').value
    }
    this.value = v
  }

  renderCallback() {
    const widthFloat = '100%';
    const { active, styles, value, error, mm, dd, yyyy } = this.props,
      _class = 'dnx-shadow dnx-date-picker'
    const className = active ? 'dnx-float dnx-float-active' : 'dnx-float'
    return (
      <div class={_class} onClick={this.handleClick}>
        <div class="input-container test">
          <input type="text" placeholder="MM" onChange={this.validateAndSet} class="MM form-control" value={mm} maxlength="2"></input>
          <span>/</span>
          <input type="text" placeholder="DD" onChange={this.validateAndSet} class="DD form-control" value={dd} maxlength="2"></input>
          <span>/</span>
          <input type="text" placeholder="YYYY" onChange={this.validateAndSet} class="YYYY form-control" value={yyyy} maxlength="4"></input>
          <span class="dna-calendar" style={{ color: "#049fd9", fontSize: "22px", cursor: "pointer" }} onClick={this.onIconClick} />
        </div>
        <div className="text-danger">{error}</div>
        <div class={className}>
          <div class="date-picker-float">
            <div class="close-icon">
              <span class="dna-close-remove" style={{ fontSize: "20px" }} onClick={this.onIconClick} />
            </div>
            <div class="date-picker"></div>
          </div>
        </div>
      </div>
    )
  }

  renderedCallback() {
    if (!this.calendar) {
      let config = {
        "showDaysInNextAndPreviousMonths": true,
        keyboardInput: false,
        onSelect: function (date) {
          this.active = !this.active
          this.error = ''
          this.setValue(date.toString())
          emit(this, 'change')
        }.bind(this)
      }
      if (this.value) {
        const _date = new Date(this.value)
        const d = moment(_date)
        if (d.isValid()) {
          config.defaultDate = _date
          config.setDefaultDate = true
          this.setValue(this.value)
          this.querySelector('.MM').value = moment(d).format('MM')
          this.querySelector('.DD').value = moment(d).format('DD')
          this.querySelector('.YYYY').value = moment(d).format('YYYY')
        }
      }
      if (this.min) config.minDate = new Date(Date.parse(this.min))
      if (this.max) config.maxDate = new Date(Date.parse(this.max))
      this.calendar = new Calendar(config);
    }
    this.shadowRoot.querySelector('.date-picker').insertBefore(this.calendar.el, null);
  }

  onIconClick = e => {
    this.active = !this.active
  }

  handleClick(e) {
    const nodeClasses = e.target.classList
    if (nodeClasses.contains("dropdown-child")) {
      const targetVal = e.target.dataset.value
      if (this.calendar && targetVal) {
        if (nodeClasses.contains('month-item'))
          this.calendar.gotoMonth(targetVal)
        else if (nodeClasses.contains('year-item'))
          this.calendar.gotoYear(targetVal)
      }
      const parent = e.target.closest(".dropdown-group")
      if (parent) parent.classList.toggle('dnx-open')
    } else if (nodeClasses.contains("dropdown-toggle")) {
      const parent = e.target.closest(".dropdown-group")
      if (parent) parent.classList.toggle('dnx-open')
    }
  }

  validateAndSet = () => {
    const r = this.shadowRoot,
      MM = r.querySelector('.MM').value,
      DD = r.querySelector('.DD').value,
      YYYY = r.querySelector('.YYYY').value
    // ISO 8601 format date
    let date = moment(`${YYYY}-${MM}-${DD}`, "YYYY-M-D", true)
    if (date.isValid()) {
      if (this.min || this.max) {
        const min = moment(new Date(this.min)),
          max = moment(new Date(this.max))
        if (min.isValid() && date < min) {
          date = INVALID
        } else if (max.isValid() && date > max) {
          date = INVALID
        }
      }
      this.setValue(date.toString())
      this.error = ''
      if (date === INVALID && this.validate)
        this.error = this.errortext
    } else {
      if (this.validate) this.error = this.errortext
      this.setValue(INVALID)
    }
    emit(this, 'change')
  }

}

define(DatePicker.is, DatePicker)
export default DatePicker



