import { props, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import NoShadowRoot from "../../utils/NoShadowRoot"

const Component = withComponent(withPreact());
const React = { createElement: h }

class AlertBetaRelease extends NoShadowRoot(Component) {
    constructor() {
        super()
        this._clickLearnMore = this._clickLearnMore.bind(this)
    }

    static get is() {
        return "dnx-alert-beta-release"
    }

    static props = {
        msg: { ...props.string, ...{ attribute: true, default: 'Beta Release' } },
        type: { ...props.string, ...{ attribute: true, default: 'beta' } },
        corner: { ...props.boolean, ...{ attribute: true, default: false } },
    }

    _clickLearnMore() {
        emit(this, 'learnMore')
    }

    renderCallback() {
        const { msg, type, corner } = this.props

        return (
            <div class={`dnx-alert-beta-release ${type} ${corner ? 'roundedBorder' : ''}`}>
                <span class="msg-text">{msg}</span>
                <a href="#" class="text-more-info"
                    onClick={this._clickLearnMore}>Learn More</a>
            </div>
        )
    }
}

define(AlertBetaRelease.is, AlertBetaRelease)
export default AlertBetaRelease


