import { props, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import NoShadowRoot from "../../utils/NoShadowRoot"

const Component = withComponent(withPreact());
const React = { createElement: h }

class Progress extends NoShadowRoot(Component) {
    constructor() {
        super()
    }

    static get is() {
        return "dnx-progress"
    }

    static props = {
        value: { ...props.number, ...{ attribute: true, default: 0 } },
        error: { ...props.boolean, ...{ attribute: true, default: false } }
    }

    renderCallback() {
        const { value, error } = this.props
        const _stateClass = error
            ? 'error'
            : value === 100
                ? 'success'
                : 'progress'

        return (
            <div class='dnx-progress'>
                <div class='meter'>
                    <span class={_stateClass} style={{ width: `${value}%` }}></span>
                </div>
                <div class="value">
                    {error
                        ? null
                        : value === 100
                            ? <i class='dna-check-mark'></i>
                            : <span>{value}%</span>
                    }
                </div>
            </div>
        )
    }
}

define(Progress.is, Progress)
export default Progress


