import { props, prop, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import stylesRaw from "./button.less"
import NoShadowRoot from "../../utils/NoShadowRoot"

const Component = withComponent(withPreact());
const React = {createElement: h}

class Button extends NoShadowRoot(Component) {
  static get is() {
    return "dnx-button"
  }
  static props = {
    disabled: props.boolean,
    label: { ...props.string, ...{ default: "text" } },
    state: props.string
  }
  renderCallback() {
    const { label, disabled, state } = this.props;
    const className = `btn btn-${state} dnx-btn`
    return (
      <div>
        <style>{stylesRaw}</style>
        <button
          type="button"
          className={className}
        >
          {label}
        </button>
      </div>
    )
  }
}

define(Button.is, Button)
export default Button



