import define from "../../utils/define"

const fragment = document.createElement('template');
fragment.innerHTML = `
    <input class='panel-hidden-switch'></input>
    <label class='panel-label'>
      <div class='panel-label-left'>
        <span class='panel-arrow'>
          <i class="arrow-down fa fa-angle-down"></i>
        </span>
        <span class='panel-title'></span>
        <span class='panel-required'>*</span>
        <span class='panel-complete'>
          <i class='dna-check-mark'></i>
        </span>
        <span class='panel-left'></span>
      </div>
      <span class='panel-right'></span>
    </label>
    <section class='tab-content'>
      <div></div>
    </section>
  `;

class AccordionPanel extends HTMLElement {

  static get is() {
    return "dnx-accordion-panel"
  }

  constructor() {
    super()
  }

  /**
  * Monitor list of attributes for changes
  * @attribute {string} title - the title for the accordion panel
  * @attribute {boolean} required - sets the status as required
  * @attribute {boolean} complete - sets the status as complete
  * @attribute {string} complete - sets the status as complete
  * @attribute {boolean} complete - sets the status as complete
  */
  static get observedAttributes() {
    return [
      'title',
      'required',
      'complete',
      'left',
      'right'
    ];
  }

  set title(v) {
    this.setAttribute('title', v)
  }

  get title() {
    if (this.hasAttribute('title')) {
      return this.getAttribute('title')
    } else {
      return ''
    }
  }

  set required(value) {
    value = Boolean(value);
    if (value)
      this.setAttribute('required', '')
    else
      this.removeAttribute('required')
  }

  get required() {
    return this.hasAttribute('required')
  }

  set complete(value) {
    value = Boolean(value);
    if (value)
      this.setAttribute('complete', '')
    else
      this.removeAttribute('complete')
  }

  get complete() {
    return this.hasAttribute('complete')
  }

  set left(v) {
    this.setAttribute('left', v)
  }

  get left() {
    if (this.hasAttribute('left')) {
      return this.getAttribute('left')
    } else {
      return ''
    }
  }

  set right(v) {
    this.setAttribute('right', v)
  }

  get right() {
    if (this.hasAttribute('right')) {
      return this.getAttribute('right')
    } else {
      return ''
    }
  }

  // Respond to attribute changes.
  attributeChangedCallback(attr, oldValue, newValue) {
    switch (attr) {
      case 'title':
      case 'required':
      case 'complete':
      case 'left':
      case 'right':
        if (this._connected) this.renderLabel()
        break
      default:
        break
    }
  }

  connectedCallback() {
    /** render */
    this._connected = true
    const panelNode = fragment.content.cloneNode(true)
    const contentNode = panelNode.querySelector('.tab-content')
    const contentDiv = panelNode.querySelector('.tab-content > div')
    const panelCheckbox = panelNode.querySelector('input[type=checkbox].panel-hidden-switch')
    const panelRadio = panelNode.querySelector('input[type=radio].panel-hidden-switch')
    while (this.childNodes.length > 0) {
      contentDiv.appendChild(this.childNodes[0]);
    }
    this.appendChild(panelNode)
    this.renderLabel()
  }

  disconnectedCallback() {
    /** Remove event listeners * /
    /** Cleanup */
  }

  renderLabel() {
    const title = this.getAttribute('title') || '',
      required = this.hasAttribute('required'),
      complete = this.hasAttribute('complete'),
      left = this.getAttribute('left') || '',
      right = this.getAttribute('right') || '',
      titleNode = this.querySelector('label.panel-label .panel-title'),
      leftNode = this.querySelector('label.panel-label .panel-left'),
      rightNode = this.querySelector('label.panel-label .panel-right'),
      requiredNode = this.querySelector('label.panel-label .panel-required'),
      completeNode = this.querySelector('label.panel-label .panel-complete')

    if (titleNode) titleNode.innerText = title
    if (leftNode) leftNode.innerText = left
    if (rightNode) rightNode.innerText = right
    if (requiredNode) {
      if (required) requiredNode.classList.remove('hide')
      else requiredNode.classList.add('hide')
    }
    if (completeNode) {
      if (complete) completeNode.classList.remove('hide')
      else completeNode.classList.add('hide')
    }
  }

}

define(AccordionPanel.is, AccordionPanel)
export default AccordionPanel



