import define from "../../utils/define"
import { generateShortID } from "../../utils/helpers"

class Accordion extends HTMLElement {

  static get is() {
    return "dnx-accordion"
  }

  constructor() {
    super()
    this.uuid = generateShortID()
  }

  /**
  * Monitor list of attributes for changes
  * @attribute {boolean} multi - whether the accordion allows multi open panels
  * @attribute {string} openpanels - serialised array of all open panels, if multi is false, only first panel in array is open
  */
  static get observedAttributes() {
    return [
      'multi',
      'openpanels' // TODO
    ];
  }

  set multi(value) {
    value = Boolean(value);
    if (value)
      this.setAttribute('multi', '')
    else
      this.removeAttribute('multi')
  }

  // coerce into boolean
  get multi() {
    return this.hasAttribute('multi')
  }

  set openpanels(value) {
    let _value;
    if (Array.isArray(value)) {
      _value = JSON.stringify(value)
    } else {
      _value = JSON.stringify([])
    }
    this.setAttribute('openpanels', _value);
  }

  // coerce into array
  get openpanels() {
    let _openpanels
    if (this.hasAttribute('openpanels')) {
      const a = JSON.parse(this.getAttribute(openpanels))
      _openpanels = Array.isArray(_a) ? _a : []
    } else {
      _openpanels = []
    }
    return _openpanels
  }

  // Respond to attribute changes.
  attributeChangedCallback(attr, oldValue, newValue) {
    switch (attr) {
      case 'multi':
        if (oldValue !== newValue) {
          this.setType()
        }
        break
      default:
        break
    }
  }

  connectedCallback() {
    /** render */
    /** Add Event listeners */

    /** Logic for children */
    Promise.all([customElements.whenDefined('dnx-accordion-panel')])
      .then(_ => {
        this.linkPanels()
      })
  }

  disconnectedCallback() {
    /** Remove event listeners * /
    /** Cleanup */
  }

  linkPanels() {
    const panels = this.allPanels()
    this.setType()
    panels.forEach((_panel, i) => {
      const
        _input = _panel.querySelector('input.panel-hidden-switch'),
        _label = _panel.querySelector('label.panel-label'),
        _content = _panel.querySelector('section.tab-content')

      _panel.dataset.idx = i.toString()
      _input.setAttribute('id', `tab-${this.uuid}-${i}`)
      _input.setAttribute('name', `${this.uuid}`)
      _label.setAttribute('for', `tab-${this.uuid}-${i}`)
      if (i === panels.length - 1) _content.style.borderBottom = "1px solid #e9e9e9"

      // Event listeners
      // checkbox | radio button events
      _input.addEventListener('change', function (e) {
        if (e.target.checked) {
          // If radio button ( not multi )
          if (!this.multi) {
            const allPanelsContent = this.querySelectorAll('dnx-accordion-panel section.tab-content')
            allPanelsContent.forEach(function (cntnt) {
              cntnt.style.maxHeight = null
            })
          }
          _content.style.maxHeight = _content.scrollHeight + "px"
        } else {
          _content.style.maxHeight = null
        }
      }.bind(this));

    })
  }

  setType() {
    const type = this.multi ? "checkbox" : "radio"
    const allInputs = Array.from(this.querySelectorAll('dnx-accordion-panel input.panel-hidden-switch'))
    allInputs.forEach(function (input) {
      input.setAttribute('type', type)
    })
  }

  allPanels() {
    return Array.from(this.querySelectorAll('dnx-accordion-panel'))
  }
}

define(Accordion.is, Accordion)
export default Accordion



