    generateLegendData() {
        const { names, data, topValue, bottomValue, topname, bottomname, barcolor } = this;
        let legendData = [];
        if (data !== null) {
            names.map((item, key) => {
                let entry = { title: item, color: barcolor[key] };
                legendData.push(entry);
            });
        }
        if (topValue) {
            let entry = { title: topname, color: '#ee0000' };
            legendData.push(entry);
        }
        if (bottomValue) {
            let entry = { title: bottomname, color: '#58585b' };
            legendData.push(entry);
        }
        return legendData;
    }
