
import { h, prop, emit, props, Component } from "skatejsv4";
import NoShadowRoot from "../../utils/NoShadowRoot"
import define from "../../utils/define"
import DoubleDonutStyle from "./DoubleDonutStyleJS.js"
import * as defaults from "./defaults"

const React = { createElement: h }
const obj1 = {}, obj2 = {}, obj3 = {}, obj4 = {};

const colorPrim = defaults.colorPrim;
const colorSec = defaults.colorSec;

class DoubleDonut extends NoShadowRoot(Component) {
    constructor() {
        super();
        this.getConfig3 = this.getConfig3.bind(this);
        this.getConfig4 = this.getConfig4.bind(this);
        this.SecondRingData = this.SecondRingData.bind(this);
        this._onClickSlice = this._onClickSlice.bind(this);
        this.initialData = this.initialData.bind(this);
    }
    static get is() {
        return "dnx-doubledonut-chart"
    }
    /**
     * DoubleDonut description.
     * @memberof DnxDonut
     * @param {object} data Data object -> data = [{ type: "iOS", percent: 70, color: "#ff9e01", subs: [{type: "Apple 6",percent: 15,color: "#b0de26"}...]},...]
     * @param {string} maintitle Chart Title.
     * @param {string} internaltitle Title to be displayed inside pie chart.
     * @param {string} internalvalue Value to be displayed inside pie chart.
     * @param {string} legendposition Position of the legend (*bottom* or *right*)
     * @param {string} labels Labels of categories (*true* or *false*)
     */
    static get props() {
        return {
            //...super.props,
            height: prop.string({
                attribute: true,
                default: "350px"
            }),
            width: prop.string({
                attribute: true,
                default: "100%"
            }),
            data: prop.object({
                attribute: true,
                default: null
            }),
            selected: prop.string(),
            legendposition: prop.string({
                attribute: true,
                default: 'bottom'
            }),
            labels: prop.boolean({
                attribute: true,
                default: false
            }),
            callouttext: prop.string({
                attribute: true,
                default: ""
            }),
            tooltiptext: prop.string({
                attribute: true,
                default: ""
            }),
            piecolor: prop.array({
                attribute: true,
                default: colorPrim
            }),
            internaltext: prop.array({
                attribute: true,
                default: [{ position: '54%', align: 'center', size: 32, bold: true, text: '', color: '#58585b' }]
            }),
            legends: prop.boolean({
                attribute: true,
                default: true
            }),
            exprt: prop.boolean({
                attribute: true,
                default: false
            })
            /*
            chart: prop.object({
                attribute: true,
                default: {}
            })*/
        }
    }
    _ref3 = new WeakMap();
    _ref4 = new WeakMap();
    renderedCallback() {
        super.connectedCallback();
        this.InitHandler = AmCharts.addInitHandler(function(chart) {
            // init holder for nested charts
            if (AmCharts.nestedChartHolder === undefined)
                AmCharts.nestedChartHolder = {};

            if (chart.bringToFront === true) {
                chart.addListener("init", function(event) {
                    // chart inited
                    var chart = event.chart;
                    var div = chart.div;
                    var parent = div.parentNode;

                    // add to holder
                    if (AmCharts.nestedChartHolder[parent] === undefined)
                        AmCharts.nestedChartHolder[parent] = [];
                    AmCharts.nestedChartHolder[parent].push(chart);

                    // add mouse mouve event

                    chart.div.addEventListener('mousemove', function() {

                        // calculate current radius
                        var x = Math.abs(chart.mouseX - (chart.realWidth / 2));
                        var y = Math.abs(chart.mouseY - (chart.realHeight / 2));
                        var r = Math.sqrt(x * x + y * y);

                        // check which chart smallest chart still matches this radius
                        var smallChart;
                        var smallRadius;
                        for (var i = 0; i < AmCharts.nestedChartHolder[parent].length; i++) {
                            var checkChart = AmCharts.nestedChartHolder[parent][i];

                            if ((checkChart.radiusReal < r) || (smallRadius < checkChart.radiusReal)) {
                                checkChart.div.style.zIndex = 1;
                            }
                            else {
                                if (smallChart !== undefined)
                                    smallChart.div.style.zIndex = 1;
                                checkChart.div.style.zIndex = 2;
                                smallChart = checkChart;
                                smallRadius = checkChart.radiusReal;
                            }

                        }
                    }, false);
                });
            }

        }, ["pie"]);

        const config3 = this.getConfig3();
        const config4 = this.getConfig4();
        const refHere3 = this._ref3.get(obj3);
        const refHere4 = this._ref4.get(obj4);
        this.chart3 = AmCharts.makeChart(refHere3, config3);
        this.chart4 = AmCharts.makeChart(refHere4, config4);
        this.chart4.dataProvider = this.SecondRingData(0);
        this.chart4.validateData();
    }
    getConfig3() {
        const { legendposition, labels, callouttext, tooltiptext, data, exprt } = this;
        return {
            "type": "pie",
            "theme": "light",
            "bringToFront": true,
            "fontFamily": "CiscoSans",
            "fontSize": 12,
            "startDuration": 0,
            "pullOutRadius": 0,
            "addClassNames": true,
            "labelsEnabled": false,//labels,
            "valueField": "percent",
            "outlineColor": "white",
            "outlineAlpha": 0.8,
            "outlineThickness": 2,
            "colorField": "color",
            "labelColorField": "#58585b",
            "dataProvider": this.initialData(),
            "titleField": "title",
            "valueField": "value",
            "colorField": "color",
            "labelRadius": 30,
            "labelColor": "#58585b",
            "radius": 109,
            "innerRadius": 95,
            "labelText": callouttext,
            "balloonText": tooltiptext,
            "balloon": {
                "adjustBorderColor": true,
                "borderColor": "#d8d9da",
                "borderAlpha": 0.1,
                "animationDuration": 0,
                "borderThickness": 1,
                "color": "#626469",
                "fadeOutDuration": 0.3,
                "cornerRadius": 0,
                "fillColor": "#FFFFFF",
                "fixedPosition": true,
                "fillAlpha": 1,
                "horizontalPadding": 8,
                "shadowAlpha": 0.1,
                "shadowColor": "#000000",
                "textAlign": "middle",
                "verticalPadding": 8,
                "fontFamily": "CiscoSans",
                "fontSize": 12
            },
            "allLabels": this.getallLabels(),
            /* NOT SUPPORTED DUE TO DOUBLE CHART. NEED A SMART GUY HERE.
            "export": {
                "enabled": exprt,
                "fileName": "DNAC-Chart",
                "menu": [{
                    "format": "JPG",
                    "label": "Export",
                    "title": "Export chart to JPG",
                    "fileName": "DNAC-Chart"
                }]
            },*/
            "listeners": [{
                "event": "rendered",
                "method": (event) => {
                    this.updateLabels(event);

                }
            }, {
                "event": "drawn",
                "method": (event) => {
                    this.updateLabels(event);
                }
            }, {
                "event": "clickSlice",
                "method": (event) => {
                    const { chart4 } = this;
                    let selected;
                    let chart = event.chart;
                    if (event.dataItem.dataContext.id != undefined) {
                        selected = event.dataItem.dataContext.id;
                    } else {
                        selected = undefined;
                    }
                    chart4.dataProvider = this.SecondRingData(selected);
                    chart4.validateData();

                }
            }]
        }
    }
    getallLabels() {
        const { internaltext, data } = this;
        let allLabels = [];
        /*
        if (data === null) {
            let entry = {
                "y": "42%",
                "align": "center",
                "size": 12,
                "bold": false,
                "text": "No data available",
                "color": "#CACACA"
            };
            allLabels.push(entry);
        } else {*/
        internaltext.map((item, key) => {
            let entry = { y: item.position, align: 'center', size: item.size, bold: item.bold, text: item.text, color: item.color };
            allLabels.push(entry);
        })
        //}
        return allLabels;
    }
    getConfig4() {
        const { maintitle, legendposition, labels, callouttext, tooltiptext } = this;
        return {
            "type": "pie",
            "fontFamily": "CiscoSans",
            "bringToFront": true,
            "labelsEnabled": false,
            "dataProvider": [],
            "startDuration": 1,
            "pullOutRadius": 0,
            "fontSize": 12,
            "titleField": "title",
            "valueField": "value",
            "colorField": "color",
            "radius": 137,
            "innerRadius": 111,
            "outlineColor": "transparent",
            "outlineAlpha": 1,
            "outlineThickness": 2,
            "labelText": callouttext,
            "balloonText": tooltiptext,
            "balloon": {
                "adjustBorderColor": true,
                "borderColor": "#d8d9da",
                "borderAlpha": 0.1,
                "animationDuration": 0,
                "borderThickness": 1,
                "color": "#626469",
                "fadeOutDuration": 0.3,
                "cornerRadius": 0,
                "fillColor": "#FFFFFF",
                "fixedPosition": true,
                "fillAlpha": 1,
                "horizontalPadding": 8,
                "shadowAlpha": 0.1,
                "shadowColor": "#000000",
                "textAlign": "middle",
                "verticalPadding": 8,
                "fontFamily": "CiscoSans",
                "fontSize": 12
            },
            "balloonFunction": function(info, graph) {
                if (info.color === "transparent") {
                    return '';
                } else {
                    return graph;
                }
            }
        }
    }

    updateLabels(selected) {
        const { data } = this;
        const colorList = [];
        if (data !== null) {
            data.map((item, key) => {
                if (selected === key) {
                    if (item.subs !== undefined) {
                        item.subs.map((item2, key2) => {
                            //colorSec[item][item2];
                            colorList.push(colorSec[item][item2]);
                        })
                    }
                } else {
                    //colorList.push((this.colors) ? this.colors[key] : colorPrim[key]);
                    colorList.push(colorPrim[key]);
                }
            })
            Array.from(this.shadowRoot.querySelectorAll('.amcharts-pie-label')).forEach((element, index) => {
                const transform = element.getAttribute('transform');
                const textAnchor = element.getAttribute('text-anchor');
                const color = colorList[index];
                const newTransformx = transform.split('(').pop().split(',').shift();
                const newTransformy = transform.split(',').pop().split(')').shift();
                const newTransformNumx = parseInt(newTransformx);
                const newTransformNumy = parseInt(newTransformy);
                element.setAttribute('transform', (textAnchor == 'start') ? `translate(${newTransformNumx + 10},${newTransformNumy})` : `translate(${newTransformNumx - 10},${newTransformNumy})`)
                const circle = document.createElementNS('http://www.w3.org/2000/svg', "circle");
                circle.setAttribute('r', '4');
                circle.setAttribute('cx', '0');
                circle.setAttribute('cy', '0');
                circle.setAttribute('fill', color);
                circle.setAttribute('stroke', color);
                circle.setAttribute('stroke-width', '1');
                circle.setAttribute('stroke-opacity', '1');
                circle.setAttribute('transform', transform);
                circle.setAttribute('class', 'amcharts-legend-marker');
                element.parentNode.appendChild(circle);
            });
        }
    }
    initialData(selected) {
        const { data } = this;
        const initialData = [];
        if (data === null) {
            initialData.push({
                title: 'No Data',
                value: 100,
                //color: (this.colors) ? this.colors[key] : colorPrim[key],
                color: '#CACACA',
                id: 1
            })
        } else {
            data.map((item, key) => {
                initialData.push({
                    title: item.title,
                    value: item.value,
                    //color: (this.colors) ? this.colors[key] : colorPrim[key],
                    color: colorPrim[key],
                    id: key
                })
            })
        }
        return initialData;
    }
    SecondRingData(selected) {
        this._onClickSlice(selected);
        const { data } = this;
        const chartData = [];
        if (data !== null) {
            data.map((item1, key1) => {
                if (key1 == selected) {
                    if (data[key1].subs === undefined) {
                        chartData.push({
                            title: item1.title,
                            value: item1.value,
                            color: 'transparent',
                            id: key1
                        });
                    } else {
                        data[key1].subs.map((item, key) => {
                            chartData.push({
                                title: item.title,
                                value: item.value,
                                color: colorSec[key1][key],
                                //color: item.color,
                                id: key
                            });
                        })
                    }
                } else {
                    chartData.push({
                        title: item1.title,
                        value: item1.value,
                        color: 'transparent',
                        id: key1
                    });
                }
            })
            return chartData;
        }
    }
    /**
     * @memberof Double Donut
     * @emits {onClickSlice} It returns the slice clicked 
     */
    _onClickSlice(event) {
        emit(this, 'onClickSlice', {
            detail: {
                data: event
            }
        })
    }
    renderCallback() {
        const { width, height, data, labels, legends } = this;
        const _styles = DoubleDonutStyle;
        let legend;
        let count = 0;
        if (!labels && data !== null && legends) {
            legend = (<div class="amChartsLegend amcharts-legend-div" style="width: 500px; height: 72px; position: relative; margin: 0px auto;">
                <svg version="1.1" style="position: absolute; width: 500px; height: 71.5px;">
                    <desc>JavaScript chart by amCharts 3.20.14</desc>
                    <g transform="translate(20,0)">
                        <path cs="100,100" d="M0.5,0.5 L459.5,0.5 L459.5,38.5 L0.5,38.5 Z" fill="#FFFFFF" stroke="#000000" fill-opacity="0" stroke-width="1" stroke-opacity="0" class="amcharts-legend-bg"></path>
                        <g transform="translate(95,11)">
                            {data.map((item, key) => {
                                const length = item.title.length;
                                let yposition = (key >= 4 && key < 8) ? 20 : (key >= 8 && key < 12) ? 40 : (key >= 12) ? 60 : 0;
                                if (key == 4 || key == 8 || key == 12) { count = 0 }
                                count = count + 1;
                                return (<g aria-label={item.title} transform={`translate(${(count - 1) * 87},${yposition})`}>
                                    <circle r="4" cx="0" cy="0" fill={colorPrim[key]} stroke={colorPrim[key]} stroke-width="1" stroke-opacity="1" transform="translate(4,9)" class="amcharts-legend-marker"></circle>
                                    <g transform="translate(4,9)" visibility="hidden" class="amcharts-legend-switch">
                                        <path cs="100,100" d="M-1.5,-1.5 L2.5,2.5" fill="none" stroke="#FFFFFF" stroke-width="3"></path>
                                        <path cs="100,100" d="M-1.5,2.5 L2.5,-1.5" fill="none" stroke="#FFFFFF" stroke-width="3"></path>
                                    </g>
                                    <text y="6" fill="#58585b" font-family="CiscoSans" font-size="12px" opacity="1" text-anchor="start" class="amcharts-legend-label" transform={`translate(${14},6)`}>
                                        <tspan y="6" x="0">{item.title}</tspan>
                                    </text>
                                    <rect x="8" y="0" width="46.609375" height="19" rx="0" ry="0" stroke-width="0" stroke="none" fill="#fff" fill-opacity="0.005"></rect>
                                </g>)
                            })}
                        </g>
                    </g>
                </svg>
            </div>)
        } else {
            legend = <span style={{ display: 'none' }} />
        }
        return [
            <style>{_styles}</style>,
            <div>
                <div id="dnx-doubledonut-charts" style={`width: ${width}; height: ${height}; position: relative; margin: 0 auto;`}>
                    <div id="chart3" ref={_r => this._ref3.set(obj3, _r)} class="chartdiv" style={`width: ${width}; height: ${height};`}></div>
                    <div id="chart4" ref={_r => this._ref4.set(obj4, _r)} class="chartdiv" style={`width: ${width}; height: ${height};`}></div>
                </div>
                {legend}
            </div>
        ];
    }
}

define(DoubleDonut.is, DoubleDonut);
export default DoubleDonut;



