
import { h, prop, emit, props, Component } from "skatejsv4";
import NoShadowRoot from "../../utils/NoShadowRoot"
import BarStyle from './BarStyleJS.js';
import define from "../../utils/define";
import * as defaults from './defaults'

const React = { createElement: h }
const colorPrim = defaults.colorPrim;
const obj = {}, obj2 = {}, obj3 = {}, objl = {}, objr = {}, objb = {};

class Bar extends NoShadowRoot(Component) {
    constructor() {
        super();
        this.getConfig = this.getConfig.bind(this);
        this.Move2 = this.Move2.bind(this);
    }
    static get is() {
        return "dnx-bar-chart"
    }
    _ref = new WeakMap();
    _refb = new WeakMap();
    _graph2 = new WeakMap();
    _line1 = new WeakMap();
    _line2 = new WeakMap();
    _graph3 = new WeakMap();

    /**
     * Constructor for DnxBar
     * @memberof DnxBar
     * 
     * @param {object} data Data object -> data = [{ type: "iOS", percent: 70, color: "#ff9e01", subs: [{type: "Apple 6",percent: 15,color: "#b0de26"}...]},...]
     * @param {array} topValue 
     * @param {array} bottomValue 
     * @param {array} names 
     * @param {string} topname 
     * @param {string} bottomname 
     * @param {boolean} rotate 
     * @param {boolean} stack 
     * @param {string} xformat 
     * @param {array} barcolor 
     * @param {string} width 
     * @param {number} columnwidth  
     * @param {string} height 
     */

    static get props() {
        return {
            data: prop.object({
                attribute: true,
                default: null
            }),
            topValue: prop.array({
                attribute: true,
                default: null
            }),
            bottomValue: prop.array({
                attribute: true,
                default: null
            }),
            names: prop.array({
                attribute: true,
                default: [""]
            }),
            topname: prop.string({
                attribute: true,
                default: ""
            }),
            bottomname: prop.string({
                attribute: true,
                default: ""
            }),
            rotate: prop.boolean({
                attribute: true,
                default: false
            }),
            stack: prop.string({
                attribute: true,
                default: "none"
            }),
            xformat: prop.string({
                attribute: true,
                default: "text"
            }),
            barcolor: prop.array({
                attribute: true,
                default: colorPrim
            }),
            width: prop.string({
                attribute: true,
                default: "100%"
            }),
            columnwidth: prop.number({
                attribute: true,
                default: 7
            }),
            height: prop.string({
                attribute: true,
                default: "300px"
            }),
            xtitle: prop.string({
                attribute: true,
                default: ""
            }),
            ytitle: prop.string({
                attribute: true,
                default: ""
            }),
            legends: prop.boolean({
                attribute: true,
                default: true
            }),
            chart: prop.object({
                attribute: false
            }),
            exprt: prop.boolean({
                attribute: true,
                default: false
            })
        }
    }
    updatedCallback(previousProps) {
        if (!previousProps) {
            return true;
        }
        const namesAndSymbols = Object.getOwnPropertySymbols
            ? Object.getOwnPropertySymbols(previousProps).concat(Object.getOwnPropertyNames(previousProps))
            : Object.getOwnPropertyNames(previousProps);
        for (let nameOrSymbol of namesAndSymbols) {
            if (!Object.is(previousProps[nameOrSymbol], this[nameOrSymbol])) {
                return true;
            }
        }
    }
    renderedCallback() {
        super.connectedCallback();
        const config = this.getConfig();
        const refHere = this._ref.get(obj);
        this.chart = AmCharts.makeChart(refHere, config);
    }
    getConfig() {
        const { rotate, stack, xformat, xtitle, ytitle, legends, exprt } = this;
        return {
            "type": "serial",
            "theme": "light",
            "marginTop": 20,
            "trendLines": [],
            "dataDateFormat": "MM/DD/YYYY HH:NN",
            "graphs": this.getGraphs(),
            "guides": [],
            "color": defaults.fontColors["slate-grey-two"],
            "fontFamily": "CiscoSans",
            "fontSize": 10,
            "addClassNames": true,
            "valueAxis": [{
                //"labelFunction": this.formatLabel()
            }],
            "rotate": rotate,
            "valueAxes": [{
                "labelsEnabled": true,
                "stackType": stack,
                "title": ytitle,
                "titleBold": false,
                "titleColor": "#626469",
                "titleFontSize": 10,
                "axisAlpha": 0,
                "position": "left",
                "autoGridCount": true,
                "gridColor": "#a7a9ab",
                "gridAlpha": 0.3,
                "gridThickness": 1,
                "fontFamily": "CiscoSans",
                "fontSize": 10,
                "minimum": 0
            }],
            "categoryField": "date",
            "categoryAxis": {
                "startOnAxis": true,
                "position": "bottom",
                "title": xtitle,
                "titleColor": "#626469",
                "titleBold": false,
                "titleFontSize": 10,
                "gridPosition": "start",
                "autoWrap": false,
                "minHorizontalGap": 0,
                "color": "#626469",
                "fontFamily": "CiscoSans",
                "fontSize": 10,
                "gridAlpha": 0,
                "axisAlpha": 0,
                "minPeriod": "hh",
                "parseDates": (xformat === "text") ? false : true,
                "minorGridEnabled": true,
                "labelFunction": function (valueText, date, categoryAxis) {
                    if (xformat === "text") {
                        return valueText
                    } else {
                        let timeDate = new Date(Date.parse(date));
                        let timeDateHr = timeDate.getHours();
                        let timeDateHrDis = (timeDateHr > 12) ? ((timeDateHr + 11) % 12 + 1) + 'p' : ((timeDateHr + 11) % 12 + 1) + 'a'
                        return timeDateHrDis;
                    }
                }
            },
            "chartCursor": {
                "enabled": false,
                "balloonPointerOrientation": "horizontal",
                "selectWithoutZooming": false,
                "cursorAlpha": 0,
                "valueLineAlpha": 0,
                "selectWithoutZooming": true,
                "bulletSize": 0,
                "zoomable": false,
                "categoryBalloonEnabled": false,
                "categoryBalloonDateFormat": "YYYY",
                "cursorColor": "#000000",
                "fullWidth": true
            },
            "chartScrollbar": {
                "enabled": false, //scrollBar
            },
            "responsive": {
                //"enabled": true
            },
            "allLabels": [],
            "legend": {
                "enabled": legends,
                "autoMargins": false,
                "equalWidths": false,
                "horizontalGap": 8,
                "markerSize": 8,
                "valueWidth": 0,
                "position": "bottom",
                "marginTop": -5,
                "fontSize": 12,
                "fontFamily": "CiscoSans",
                "align": "left",
                "markerType": "circle",
                "data": this.generateLegendData(),
                "color": "#58585b",
                "markerLabelGap": 6,
                "spacing": 20
            },
            "dataProvider": this.initialData(),
            "export": {
                "enabled": exprt,
                "fileName": "DNAC-Chart",
                "menu": [{
                    "format": "JPG",
                    "label": "Export",
                    "title": "Export chart to JPG",
                    "fileName": "DNAC-Chart"
                }]
            },
            "listeners": [{
                "event": "init",
                "method": (e) => {
                    e.chart.removeListener(e.chart.chartScrollbar, "zoomed", e.chart.handleScrollbarZoom);
                }
            }, {
                "event": "rendered",
                "method": (event) => {
                    event.chart.removeListener(event.chart.chartScrollbar, "zoomed", event.chart.handleScrollbarZoom);
                    this.updateBullets(event);
                    this.updateLegends(event);
                    this.updateXLabels(event);
                    this.updateLegendsLabels(event);
                }
            }, {
                "event": "drawn",
                "method": (event) => {
                    event.chart.removeListener(event.chart.chartScrollbar, "zoomed", event.chart.handleScrollbarZoom);
                    this.updateBullets(event);
                    this.updateLegends(event);
                    this.updateXLabels(event);
                    this.updateLegendsLabels(event);
                }
            }, {
                "event": "rollOverGraphItem",
                "method": (event) => {
                    const value = event.item.values.value;
                    const category = event.item.category;
                    let timeDateHrDis;
                    if (xformat === "text") {
                        timeDateHrDis = category;
                    } else {
                        let timeDate = new Date(Date.parse(category));
                        let timeDateHr = timeDate.getHours();
                        timeDateHrDis = (timeDateHr > 12) ? ((timeDateHr + 11) % 12 + 1) + 'p' : ((timeDateHr + 11) % 12 + 1) + 'a'
                    }
                    const b = this._refb.get(objb);
                    b.innerHTML = "<div style='padding: 5px'><div> Category: " + timeDateHrDis + "</div><br/><div><b>Value: " + value + "</b></div><div>";
                    b.style.display = "block";
                }
            }, {
                "event": "rollOutGraphItem",
                "method": (event) => {
                    const b = this._refb.get(objb);
                    b.style.display = "none";
                }
            }]
        }
    }
    generateLegendData() {
        const { names, data, topValue, bottomValue, topname, bottomname, barcolor } = this;
        let legendData = [];
        if (data !== null) {
            names.map((item, key) => {
                let entry = { title: item, color: barcolor[key] };
                legendData.push(entry);
            });
        }
        if (topValue) {
            let entry = { title: topname, color: '#ee0000' };
            legendData.push(entry);
        }
        if (bottomValue) {
            let entry = { title: bottomname, color: '#58585b' };
            legendData.push(entry);
        }
        return legendData;
    }
    getGraphs(event) {
        const { names, topValue, bottomValue, topname, bottomname, stack, columnwidth } = this;
        let getGraphsArray = [];
        if (names) {
            names.map((item, index) => {
                let element = {
                    "alphaField": "alpha",
                    "showBalloon": false,
                    "fillAlphas": 1,
                    "title": `value${index + 1}`,
                    "type": "column",
                    "valueField": `value${index + 1}`,
                    "fixedColumnWidth": columnwidth,
                    "colorField": `color${index + 1}`,
                    "lineColorField": (stack === "regular") ? null : `color${index + 1}`,
                    "lineColor": (stack === "regular") ? "#FFFFFF" : null,
                    "lineThickness": 2,
                    "fillColorsField": `color${index + 1}`
                };
                getGraphsArray.push(element);
            });
        }
        if (bottomValue) {
            let elementBottom = {
                "id": "bottom",
                "type": "smoothedLine",
                "fillAlphas": 0,
                "lineAlpha": 1,
                "showBalloon": false,
                "valueField": "bottomValue",
                "dashLength": 2,
                "lineColor": "#58585b",
                "lineThickness": 1
            }
            getGraphsArray.push(elementBottom);
        }
        if (topValue) {
            let elementTop = {
                "id": "toGraph",
                "type": "smoothedLine",
                "fillAlphas": 0,
                "fillColors": "transparent",
                "lineAlpha": 1,
                "showBalloon": false,
                "valueField": "topValue",
                "dashLength": 2,
                "lineColor": "#ee0000",
                "lineThickness": 1,
            }
            getGraphsArray.push(elementTop);
            /*
            let elementTopTop = {
                // straight red top
                "id": "toptopGraph",
                "type": "smoothedLine",
                "fillAlphas": 0.5,
                "fillColors": "rgba(238, 0, 0, 0.1)",
                //"fillColors": "transparent",
                "fillToGraph": "topGraph",
                "lineAlpha": 1,
                "showBalloon": false,
                "valueField": "toptopValue",
                "dashLength": 0,
                "lineColor": "transparent",
                //"lineThickness": 1,
                //"stackable": false
            }
            getGraphsArray.push(elementTopTop);*/
        }

        return getGraphsArray;
    }

    updateXLabels(event) {
        Array.from(this.shadowRoot.querySelectorAll('.amcharts-axis-label')).forEach((element, index) => {
            const child = element.firstChild;
            child.setAttribute('font-size', '10px');
            child.setAttribute('font-weight', '200');
            child.setAttribute('color', '#626469');
        });
    }
    updateLegendsLabels(event) {
        Array.from(this.shadowRoot.querySelectorAll('.amcharts-legend-label')).forEach((element, index) => {
            const child = element.firstChild;
            child.setAttribute('font-family', 'CiscoSans');
            child.setAttribute('font-size', '12px');
            child.setAttribute('font-weight', '200');
            child.setAttribute('color', '#58585b');
            child.setAttribute('line-height', '1.08')
        });
    }

    updateBullets(events) {
        Array.from(this.shadowRoot.querySelectorAll('.amcharts-graph-bullet')).forEach((element, index) => {
            if (element.attributes.r === undefined) {
                const oldChild = element.firstChild;
                const circle = document.createElementNS('http://www.w3.org/2000/svg', "circle");
                circle.setAttribute('r', '2');
                circle.setAttribute('cx', '0');
                circle.setAttribute('cy', '0');
                circle.setAttribute('fill', '#5682d8');
                circle.setAttribute('stroke', '#5682d8');
                circle.setAttribute('stroke-width', '2');
                circle.setAttribute('stroke-opacity', '1');
                circle.setAttribute('transform', 'translate(0,0)');
                circle.setAttribute('aria-label', ' 3p 1.125');
                const newChild = circle;
                element.replaceChild(newChild, oldChild)
            }
        });
    }
    updateLegends(events) {
        const count = this.names.length;
        Array.from(this.shadowRoot.querySelectorAll('.amcharts-legend-marker')).forEach((element, index) => {
            if (index + 1 > count) {
                const oldChild = element;
                const parent = element.parentElement;
                const line = document.createElementNS('http://www.w3.org/2000/svg', "line");
                line.setAttribute('stroke-dasharray', '2, 2');
                line.setAttribute('x1', '0');
                line.setAttribute('y1', '10');
                line.setAttribute('x2', '10');
                line.setAttribute('y2', '10');
                line.setAttribute('stroke', (index + 1 - count == 1) ? '#58585b' : '#ee0000');
                const newChild = line;
                parent.replaceChild(newChild, oldChild)
            }
        });
    }
    initialData(selected) {
        const { data, topValue, stack, bottomValue, barcolor } = this;
        let initialData = [];
        if (data) {
            let valuesLength = data[0].values.length;
            if (valuesLength > 10) {
                alert('This chart only support max 10 inputs. Contact https://github3.cisco.com/dna-platform-ui/dnx-web-components to submit an issue')
            } else {
                data.map((item, key) => {
                    let color = "#5682d8", customBullet;
                    let valuesObject = {};
                    item.values.map((element, index) => {
                        let currentIndex = index + 1;
                        let propName = 'value' + currentIndex;
                        valuesObject[propName] = element;

                    })
                    let colorObject = {};
                    barcolor.map((element, index) => {
                        let currentIndex = index + 1;
                        let propName = 'color' + currentIndex;
                        colorObject[propName] = element;
                    })
                    let restObject = { date: item.date, bottomValue: (bottomValue === null) ? null : bottomValue[key], topValue: (topValue === null) ? null : (stack === "regular") ? topValue[key] - bottomValue[key] : topValue[key], toptopValue: 15, id: key }
                    let finalObject = Object.assign(valuesObject, colorObject, restObject);
                    initialData.push(finalObject);
                });
            }
        }
        return initialData;
    }
    Move2(event) {
        const b = this._refb.get(objb);
        let positionX = event.clientX - event.currentTarget.getBoundingClientRect().left - 150;
        let positionY = event.clientY - event.currentTarget.getBoundingClientRect().top - 100;
        b.style.top = positionY + 'px';
        b.style.left = positionX + 'px';
    }
    renderCallback() {
        const { width, height } = this;
        const widthChart = width;
        const heightCont = height;
        const heightChart = "100%";
        const _styles = BarStyle;
        //console.log(this.chart);
        return [
            <style>{_styles}</style>,
            <div style={'position: relative;'}>
                <div style={`width: ${widthChart}; height: ${heightCont}; background-color: white;`}>
                    <div ref={_r => this._ref.set(obj, _r)} style={`width: ${widthChart}; height: ${heightChart}; overflow: visible; top: 10px; left: 10px; position: relative;`} onmousemove={this.Move2}></div>
                </div>
                <div id="dnx-bar-balloon" ref={_r => this._refb.set(objb, _r)} style="display: none;"></div>
            </div>
        ];
    }
}

define(Bar.is, Bar)
export default Bar





