                "cursorAlpha": 0,
                //"valueLineEnabled":false,
                "valueLineAlpha": 0,
                "selectWithoutZooming": true,
                "bulletSize": 0,
                "zoomable": false,
                "categoryBalloonEnabled": false,
                "categoryBalloonDateFormat": "YYYY",
                //"cursorAlpha": 0.1,
                "cursorColor": "#000000",
                "fullWidth": true,
                //"graphBulletSize": 2
