                    const category = event.item.category;
                    let timeDateHrDis;
                    if (xformat === "text") {
                        timeDateHrDis = category;
                    } else {
                        let timeDate = new Date(Date.parse(category));
                        let timeDateHr = timeDate.getHours();
                        timeDateHrDis = (timeDateHr > 12) ? ((timeDateHr + 11) % 12 + 1) + 'p' : ((timeDateHr + 11) % 12 + 1) + 'a'
                    }

                    const b = this._refb.get(objb);
                    b.innerHTML = "<div style='padding: 5px'><div> Time: " + timeDateHrDis + "</div><br/><div><b>Value: " + value + "</b></div><div>";
