import { props, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import NoShadowRoot from "../../utils/NoShadowRoot"
import moment from "../../libs/vendor/moment/moment"

const Component = withComponent(withPreact());
const React = { createElement: h }
const INVALID = "Invalid time"

class TimePicker extends NoShadowRoot(Component) {
    constructor() {
        super();
        this.handleClick = this.handleClick.bind(this)
    }

    static get is() {
        return "dnx-time-picker"
    }

    static props = {
        styles: { ...props.string, ...{ attribute: true, default: '' } },
        value: { ...props.string, ...{ attribute: true, default: '' } },
        hh: { ...props.string, ...{ attribute: true, default: '' } },
        mm: { ...props.string, ...{ attribute: true, default: '' } },
        error: { ...props.string, ...{ attribute: true, default: '' } },
        errortext: { ...props.string, ...{ attribute: true, default: INVALID } },
        format24: { ...props.boolean, ...{ attribute: true, default: false } },
        validate: { ...props.boolean, ...{ attribute: true, default: false } }
    }

    /**
     * renders a JS date objects time
     */
    renderCallback() {
        const { styles, value, error, format24 } = this.props,
            _class = 'dnx-shadow dnx-time-picker'
        let time, HH, mm, ampm
        if (value === INVALID) {
            const r = this.shadowRoot
            HH = r.querySelector('.HH').value || ''
            mm = r.querySelector('.mm').value || ''
            ampm = r.querySelector('.ampm-value').dataset.value || ''
        } else {
            time = moment(new Date(value));
            HH = time.isValid() ? moment(time).format('HH') : ''
            HH = parseInt(HH, 10)
            mm = time.isValid() ? moment(time).format('mm') : ''
            ampm = (typeof HH === 'number' && HH >= 12) ? "PM" : "AM"
            if (!format24) {
                HH = time.isValid() ? moment(time).format('hh') : ''
            }
        }

        return (
            <div class={_class}>
                <div class="input-container">
                    <input type="text" placeholder="12" onChange={this.validateAndSet} class="HH form-control" value={HH} maxlength="2"></input>
                    <span>:</span>
                    <input type="text" placeholder="00" onChange={this.validateAndSet} class="mm form-control" value={mm} maxlength="2"></input>
                    <span>:</span>
                    <div onClick={this.handleClick} class="dnx-dropdown dropdown dropdown-group btn-group">
                        <button type="button" class="btn-dropdown form-control  dropdown-toggle" data-toggle="dropdown">
                            <span class="dropdown-toggle-child ampm-value" data-value={ampm}>{ampm}</span>
                            <span class="fa fa-angle-down dropdown-caret dropdown-toggle-child" style="color: rgb(51, 51, 51); font-size: 16px;"></span>
                        </button>
                        <ul class="dropdown-menu" role="menu">
                            <div class="dropdown-item">
                                <li><a data-value="AM" class="AM dropdown-child">AM</a></li>
                            </div>
                            <div class="dropdown-item">
                                <li><a data-value="PM" class="PM dropdown-child">PM</a></li>
                            </div>
                        </ul>
                    </div>
                </div>
                <div className="text-danger">{error}</div>
            </div>
        )
    }

    handleClick(e) {
        const nodeClasses = e.target.classList
        if (nodeClasses.contains("dropdown-child")) {
            const targetVal = e.target.dataset.value
            const parent = e.target.closest(".dropdown-group")
            if (parent) {
                parent.classList.toggle('dnx-open')
                const ampmNode = parent.querySelector('.ampm-value')
                ampmNode.dataset.value = targetVal
                ampmNode.innerHTML = targetVal
                this.validateAndSet()
            }
        } else if (nodeClasses.contains("dropdown-toggle")) {
            const parent = e.target.closest(".dropdown-group")
            if (parent) parent.classList.toggle('dnx-open')
        }
    }

    validateAndSet = () => {
        function isValidTime(h, m) {
            return (typeof h === "number"
                && typeof m === "number"
                && h >= 1 && h <= 12
                && m >= 0 && m <= 59)
        }

        const r = this.shadowRoot,
            HH_val = r.querySelector('.HH').value,
            mm_val = r.querySelector('.mm').value,
            HH_12 = parseInt(HH_val, 10),
            mm = parseInt(mm_val, 10),
            ampm = r.querySelector('.ampm-value').dataset.value,
            time = new Date()
        if (isValidTime(HH_12, mm)) {
            let HH
            if (ampm === "PM") {
                HH = HH_12 === 12 ? HH_12 : (HH_12 + 12) % 24
            } else {
                HH = HH_12 === 12 ? 0 : HH_12
            }
            time.setHours(HH)
            time.setMinutes(mm)
            this.value = time.toString()
            this.error = ''
        } else {
            if (this.validate) this.error = this.errortext
            this.value = INVALID
        }
        this.hh = HH_val
        this.mm = mm_val
        emit(this, 'change')
    }

}

define(TimePicker.is, TimePicker)
export default TimePicker


