    generateLegendData() {
        const { names, topValue, bottomValue, topname, bottomname, barcolor } = this;
        let legendData = [];
        names.map((item, key) => {
            let entry = { title: item, color: colorPrim[key] };
            legendData.push(entry);
        });
        if (topValue) {
            let entry = { title: topname, color: '#ee0000' };
            legendData.push(entry);
        }
        if (bottomValue) {
            let entry = { title: bottomname, color: '#58585b' };
            legendData.push(entry);
        }
        return legendData;
    }
