    Move2(event) {
        const b = this._refb.get(objb);
        let positionX = event.clientX - event.currentTarget.getBoundingClientRect().left - 150;
        let positionY = event.clientY - event.currentTarget.getBoundingClientRect().top - 100;
        b.style.top = positionY + 'px';
        b.style.left = positionX + 'px';
    }
    renderCallback() {
        const { width, height } = this;
        const widthChart = width;
        const heightCont = height;
        const heightChart = "100%";
        const _styles = BubbleStyle;
        return [
            <style>{_styles}</style>,
            <div style={'position: relative;'}>
                <div style={`width: ${widthChart}; height: ${heightCont}; background-color: white;`}>
                    <div ref={_r => this._ref.set(obj, _r)} style={`width: ${widthChart}; height: ${heightChart}; overflow: visible; top: 10px; left: 10px; position: relative;`} onmousemove={this.Move2}></div>
                </div>
                <div id="dnx-bubble-balloon" ref={_r => this._refb.set(objb, _r)} style="display: none;"></div>
            </div>
        ];
    }
