import { props, emit, withComponent } from 'skatejs';
import { h } from 'preact';
import withPreact from '@skatejs/renderer-preact';
import define from "../../utils/define"
import NoShadowRoot from "../../utils/NoShadowRoot"
import noUiSlider from "./libs/nouislider"

const Component = withComponent(withPreact());
const React = { createElement: h }

class Slider extends NoShadowRoot(Component) {

    constructor() {
        super();
    }

    static get is() {
        return "dnx-slider"
    }

    static props = {
        start: { ...props.number, ...{ attribute: true, default: 0 } },
        end: { ...props.number, ...{ attribute: true, default: 100 } },
        double: { ...props.boolean, ...{ attribute: true, default: false } },
        min: { ...props.number, ...{ attribute: true, default: 0 } },
        max: { ...props.number, ...{ attribute: true, default: 100 } }
    }

    renderCallback() {
        const { start, end, double, min, max } = this.props
        return (
            <div class='dnx-slider'></div>
        )
    }

    renderedCallback() {
        const { start, end, double, min, max } = this
        if (!this.slider) {
            let config = {
                range: {
                    min: min || 0,
                    max: max || 100
                },
                tooltips: true
            }
            if (double && end) {
                if (!start) this.start = 0
                Object.assign(config, {
                    start: [start, end],
                    connect: true
                })
            } else {
                if (!start) this.start = 0
                Object.assign(config, {
                    start: start,
                    connect: "lower"
                })
            }
            var node = this.querySelector('.dnx-slider')
            this.slider = noUiSlider.create(node, config)
        }
        emit(this, 'render')
    }

}

define(Slider.is, Slider)
export default Slider



