
import { h, prop, emit, props, Component } from "skatejsv4";
import NoShadowRoot from "../../utils/NoShadowRoot"
import define from "../../utils/define";
import DonutStyle from './DonutStyle.js';
import * as defaults from './defaults'

const React = { createElement: h }
const obj = {};
const colorPrim = defaults.colorPrim;

class Donut extends NoShadowRoot(Component) {
    constructor() {
        super();
        this.getConfig = this.getConfig.bind(this);
        this.initialData = this.initialData.bind(this);
        this.updateLabels = this.updateLabels.bind(this);
    }
    static get is() {
        return "dnx-donut-chart"
    }
    _ref = new WeakMap();
    /**
     * DonutDescription.
     * @memberof DnxDonut
     * @param {object} data Data object -> data = [{ type: "iOS", percent: 70, color: "#ff9e01", subs: [{type: "Apple 6",percent: 15,color: "#b0de26"}...]},...]
     * @param {string} maintitle Chart Title.
     * @param {string} internaltitle Title to be displayed inside pie chart.
     * @param {string} internalvalue Value to be displayed inside pie chart.
     * @param {string} legendposition Position of the legend (*bottom* or *right*)
     * @param {string} labels Labels of categories (*true* or *false*)
     */

    static get props() {

        return {
            //...super.props,
            height: prop.string({
                attribute: true,
                default: "350px"
            }),
            width: prop.string({
                attribute: true,
                default: "100%"
            }),
            data: prop.object({
                attribute: true,
                default: null
            }),
            selected: prop.string(),
            legendposition: prop.string({
                attribute: true,
                default: 'bottom'
            }),
            labels: prop.boolean({
                attribute: true,
                default: false
            }),
            callouttext: prop.string({
                attribute: true,
                default: ""
            }),
            tooltiptext: prop.string({
                attribute: true,
                default: ""
            }),
            piecolor: prop.array({
                attribute: true,
                default: colorPrim
            }),
            internaltext: prop.array({
                attribute: true,
                default: [{ position: '54%', align: 'center', size: 32, bold: true, text: '', color: '#58585b' }]
            }),
            legends: prop.boolean({
                attribute: true,
                default: true
            }),
            exprt: prop.boolean({
                attribute: true,
                default: false
            })
            /*
            chart: prop.object({
                attribute: true,
                default: {}
            })*/
        }
    }
    renderedCallback() {
        super.connectedCallback();
        const config = this.getConfig();
        const refHere = this._ref.get(obj);
        const { data } = this;
        this.chart = AmCharts.makeChart(refHere, config);
    }
    getConfig() {
        const { legendposition, legends, labels, callouttext, tooltiptext, data, exprt } = this;
        return {
            "type": "pie",
            "theme": "light",
            "fontFamily": "CiscoSans",
            "fontSize": 12,
            "startDuration": 0,
            "pullOutRadius": 0,
            "addClassNames": true,
            "labelsEnabled": (data === null) ? false : labels,
            "outlineColor": "#FFFFFF",
            "outlineAlpha": 0.8,
            "outlineThickness": 2,
            "colorField": "color",
            "labelColorField": "#58585b",
            "legend": {
                "enabled": legends,
                "autoMargins": false,
                "equalWidths": false,
                "horizontalGap": 8,
                "markerSize": 8,
                "valueWidth": 0,
                "position": legendposition,
                "marginTop": 25,
                "fontFamily": "CiscoSans",
                "align": "center",
                "markerType": "circle",
                "data": this.generateLegendData(),
                "color": "#58585b",
                "markerLabelGap": 6//,
            },
            "dataProvider": this.initialData(),
            "titleField": "title",
            "valueField": "value",
            "colorField": "color",
            "labelRadius": 30,
            "labelColor": "#58585b",
            "radius": 109,
            "innerRadius": 95,
            "labelText": callouttext,
            "balloonText": tooltiptext,
            "balloon": {
                "adjustBorderColor": true,
                "borderColor": "#d8d9da",
                "borderAlpha": 0.1,
                "animationDuration": 0,
                "borderThickness": 1,
                "color": "#626469",
                "fadeOutDuration": 0.5,
                "cornerRadius": 0,
                "fillColor": "#FFFFFF",
                "fixedPosition": true,
                "fillAlpha": 1,
                "horizontalPadding": 8,
                "shadowAlpha": 0.1,
                "shadowColor": "#000000",
                "textAlign": "middle",
                "verticalPadding": 8,
                "fontFamily": "CiscoSans",
                "fontSize": 12
            },
            "allLabels": this.getallLabels(),
            "export": {
                "enabled": exprt,
                "fileName": "DNAC-Chart",
                "menu": [{
                    "format": "JPG",
                    "label": "Export",
                    "title": "Export chart to JPG",
                    "fileName": "DNAC-Chart"
                }]
            },
            "listeners": [{
                "event": "rendered",
                "method": (event) => {
                    this.updateLabels(event);
                }
            }, {
                "event": "drawn",
                "method": (event) => {
                    //this.updateLabels(event);
                }
            }, {
                "event": "clickSlice",
                "method": (event) => {
                    console.log("Clicking...");
                }
            }]
        }
    }
    getallLabels() {
        const { internaltext, data } = this;
        let allLabels = []
        /*
        if (data === null) {
            let entry = {
                "y": "42%",
                "align": "center",
                "size": 12,
                "bold": false,
                "text": "No data available",
                "color": "#CACACA"
            };
            allLabels.push(entry);
        } else {*/
        internaltext.map((item, key) => {
            let entry = { y: item.position, align: 'center', size: item.size, bold: item.bold, text: item.text, color: item.color };
            allLabels.push(entry);
        })
        //}
        return allLabels;
    }
    updateLabels(selected) {
        const colorList = [];
        const { data, piecolor } = this;
        if (data !== null) {
            data.map((item, key) => {
                if (selected === key) {
                    if (item.subs !== undefined) {
                        item.subs.map((item2, key2) => {
                            colorList.push(item2.color);
                        })
                    }
                } else {
                    //colorList.push((this.colors) ? this.colors[key] : piecolor[key]);
                    colorList.push(piecolor[key]);
                }
            })
            Array.from(this.shadowRoot.querySelectorAll('.amcharts-pie-label')).forEach((element, index) => {
                const transform = element.getAttribute('transform');
                const textAnchor = element.getAttribute('text-anchor');
                const color = colorList[index];
                const newTransformx = transform.split('(').pop().split(',').shift();
                const newTransformy = transform.split(',').pop().split(')').shift();
                const newTransformNumx = parseInt(newTransformx);
                const newTransformNumy = parseInt(newTransformy);
                element.setAttribute('transform', (textAnchor == 'start') ? `translate(${newTransformNumx + 10},${newTransformNumy})` : `translate(${newTransformNumx - 10},${newTransformNumy})`)
                const circle = document.createElementNS('http://www.w3.org/2000/svg', "circle");
                circle.setAttribute('r', '4');
                circle.setAttribute('cx', '0');
                circle.setAttribute('cy', '0');
                circle.setAttribute('fill', color);
                circle.setAttribute('stroke', color);
                circle.setAttribute('stroke-width', '1');
                circle.setAttribute('stroke-opacity', '1');
                circle.setAttribute('transform', transform);
                circle.setAttribute('class', 'amcharts-legend-marker');
                element.parentNode.appendChild(circle);
            });
            Array.from(this.shadowRoot.querySelectorAll('.amcharts-legend-label')).forEach((element, index) => {
                element.setAttribute('transform', 'translate(14,6)');
            });
        }
    }
    generateLegendData() {
        const { data, labels, piecolor } = this;
        let legendData = [];
        if (data !== null) {
            if (labels) {
                legendData = [];
            } else {
                data.map((item, key) => {
                    //let entry = { title: item.title, color: (this.colors) ? this.colors[key] : piecolor[key] };
                    let entry = { title: item.title, color: piecolor[key] };
                    legendData.push(entry);
                });
            }
            return legendData;
        }
        return legendData;
    }
    initialData(selected) {
        const { data, piecolor } = this;
        let initialData = [];
        if (data === null) {
            initialData.push({
                title: 'No Data',
                value: 100,
                //color: (this.colors) ? this.colors[key] : piecolor[key],
                color: '#CACACA',
                id: 1
            })
        } else {
            data.map((item, key) => {
                initialData.push({
                    title: item.title,
                    value: item.value,
                    color: piecolor[key],
                    id: key
                })
            })
        }
        return initialData;
    }
    /**
     * @memberof Double Donut
     * @emits {onClickSlice} It returns the slice clicked 
     */
    _onClickSlice(event) {
        emit(this, 'onClickSlice', {
            detail: {
                data: event
            }
        })
    }
    renderCallback() {
        //<dnx-top root={this}>{_styles}</dnx-top>
        const { width, height } = this;
        const _styles = DonutStyle;
        let chart;
        return [
            <style>{_styles}</style>,
            <div ref={_r => this._ref.set(obj, _r)} class="pie-chart" style={`width: ${width}; height: ${height};`}></div>
        ];
    }
}

define(Donut.is, Donut)
export default Donut




